<template>
  <div id="login">
    <div class="register_box">
      <h2>金牛视频后台管理系统</h2>
      <div class="register_phone">
        <p>用户身份验证</p>
        <el-form ref="form" v-model="fitter">
          <el-form-item label="用户名" prop="nameRules">
            <el-input v-model="fitter.usrName"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="passwordRules">
            <el-input type="password" v-model="fitter.pwd"></el-input>
          </el-form-item>
        </el-form>
        <el-button class="btn" @click="login">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import phoneicon from "@/assets/images/2.phoneicon.png";
import pwdicon from "@/assets/images/3.pwdicon.png";
export default {
  name: "login",
  data() {
    return {
      phoneicon,
      pwdicon,
      fitter: {
        usrName: "",
        pwd: ""
      },
      rules: {
        nameRules: [
          { required: true, message: "请输入账号", trigger: "blur" },
          { min: 1, max: 11, message: "账号不能超过11位", trigger: "blur" }
        ],
        passwordRules: [
          { required: true, message: "请输入密码", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    login() {
      let params = { usrName: this.fitter.usrName, pwd: this.fitter.pwd };
      if (this.checkout()) {
        this.axios.post("/api/usr/login", this.formData(params)).then(res => {
          if (res.data.code == "OK") {
            this.$message({
              message: "登录成功",
              center: true,
              offset: 100,
              type: "success"
            });
            setTimeout(() => {
              this.$router.push({
                name: "management"
              });
            }, 500);
          } else {
            this.$message({
              message: res.data.descp,
              center: true,
              offset: 100,
              type: "error"
            });
          }
        });
      }
    },
    checkout() {
      if (!this.fitter.usrName) {
        this.$message({
          message: "请输入用户名",
          center: true,
          offset: 100,
          type: "error"
        });
        return false;
      }
      if (!this.fitter.pwd) {
        this.$message({
          message: "请输入密码",
          center: true,
          offset: 100,
          type: "error"
        });
        return false;
      }
      return true;
    },
    formData(data) {
      let ret = "";
      for (let it in data) {
        ret +=
          encodeURIComponent(it) + "=" + encodeURIComponent(data[it]) + "&";
      }
      return ret;
    },
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>

<style lang="scss">
#login {
  .el-form-item__label {
    color: #fdfefd !important;
  }
  .register_box {
    color: #fdfefd;
    height: 100vh;
    background: url("../assets/images/banner.jpg");
    background-size: cover;
    text-align: center;
    padding: 100px 0;
  }
  h2 {
    margin-bottom: 40px;
    margin-right: 23%;
    font-size: 26px;
    text-align: right;
  }
  .el-alert {
    width: 200px;
    margin: 0 auto;
    position: fixed;
    top: 48%;
    left: 40%;
  }
  .register_phone {
    position: relative;
    right: -60%;
    width: 280px;
    padding: 30px;
    background: rgba(249, 249, 250, 0.2);
    box-shadow: 0 2px 12px 2px rgba(34, 34, 36, 0.8);
    border-radius: 2px;
    border: 2px solid #2268b0;
    line-height: 70px;
    color: #fdfefd;
    img {
      width: 13px;
      height: 15px;
      margin-right: 10px;
      margin-top: 25px;
    }
  }
  .register_box_item {
    display: flex;
  }
  .btn {
    width: 100%;
    margin-top: 50px;
    background: cornflowerblue !important;
    color: white !important;
  }

  .el-input__inner {
    width: 220px !important;
    border-top-width: 0px !important;
    border-left-width: 0px !important;
    border-right-width: 0px !important;
    border-bottom-width: 1px !important;
    outline: medium !important;
  }
}
</style>
