<template>
  <body class="demo-gallery loading">
    <svg class="hidden">
      <symbol id="icon-arrow" viewBox="0 0 24 24">
        <title>arrow</title>
        <polygon
          points="6.3,12.8 20.9,12.8 20.9,11.2 6.3,11.2 10.2,7.2 9,6 3.1,12 9,18 10.2,16.8 "
        />
      </symbol>
      <symbol id="icon-drop" viewBox="0 0 24 24">
        <title>drop</title>
        <path
          d="M12,21c-3.6,0-6.6-3-6.6-6.6C5.4,11,10.8,4,11.4,3.2C11.6,3.1,11.8,3,12,3s0.4,0.1,0.6,0.3c0.6,0.8,6.1,7.8,6.1,11.2C18.6,18.1,15.6,21,12,21zM12,4.8c-1.8,2.4-5.2,7.4-5.2,9.6c0,2.9,2.3,5.2,5.2,5.2s5.2-2.3,5.2-5.2C17.2,12.2,13.8,7.3,12,4.8z"
        />
        <path
          d="M12,18.2c-0.4,0-0.7-0.3-0.7-0.7s0.3-0.7,0.7-0.7c1.3,0,2.4-1.1,2.4-2.4c0-0.4,0.3-0.7,0.7-0.7c0.4,0,0.7,0.3,0.7,0.7C15.8,16.5,14.1,18.2,12,18.2z"
        />
      </symbol>
    </svg>
    <main>
      <div class="content">
        <div class="gallery">
          <div
            class="gallery__item pieces gallery__item--current"
            :style="`background-image: url(${itme1});background-size: cover`"
          ></div>
          <div
            class="gallery__item pieces"
            :style="`background-image: url(${itme2});background-size: cover`"
          ></div>
          <div
            class="gallery__item pieces"
            :style="`background-image: url(${itme3});background-size: cover`"
          ></div>
          <!-- <div
            class="gallery__item pieces"
            :style="`background-image: url(${itme4});background-size: cover`"
          ></div>
          <div
            class="gallery__item pieces"
            :style="`background-image: url(${itme5});background-size: cover`"
          ></div> -->
        </div>
        <nav class="gallery-nav" style="display: none;">
          <span class="gallery-nav__item gallery-nav__item--current"></span>
          <span class="gallery-nav__item"></span>
          <span class="gallery-nav__item"></span>
          <!-- <span class="gallery-nav__item"></span>
          <span class="gallery-nav__item"></span> -->
        </nav>
      </div>
    </main>
  </body>
</template>
<script>
import imagesLoaded from "../../static/js/imagesloaded.pkgd.min.js";
import anime from "../../static/js/anime.min.js";
import "../../static/js/main.js";
export default {
  data() {
    return {
      itme1: require("../../static/img/g1.png"),
      itme2: require("../../static/img/g2.png"),
      itme3: require("../../static/img/g3.png"),
      itme4: require("../../static/img/g4.png"),
      itme5: require("../../static/img/g5.png")
    };
  },
  mounted() {
    {
      imagesLoaded(document.body, { background: true }, () =>
        document.body.classList.remove("loading")
      );

      const getRandomInt = (min, max) =>
        Math.floor(Math.random() * (max - min + 1)) + min;
      const galleryItems = Array.from(
        document.querySelectorAll(".gallery > .gallery__item")
      );
      const navItems = Array.from(
        document.querySelectorAll(".gallery-nav > .gallery-nav__item")
      );
      const piecesObjs = [];
      let current = 0;
      let isAnimating = false;
      let animateInTimeout;
      let fx = 0;

      galleryItems.forEach(item => {
        piecesObjs.push(
          new Pieces(item, {
            pieces: { rows: 6, columns: 3 },
            delay: [0, 175]
          })
        );
      });

      navItems.forEach((navitem, pos) => {
        navitem.addEventListener("click", ev => navigate(ev, pos));
      });

      const navigate = (ev, pos) => {
        ev.preventDefault();
        if (isAnimating || current === pos) {
          return false;
        }
        isAnimating = true;

        navItems[current].classList.remove("gallery-nav__item--current");
        navItems[pos].classList.add("gallery-nav__item--current");

        piecesObjs[current].animate({
          duration: 800,
          easing: [0.2, 1, 0.3, 1],
          delay: (t, i, l) => {
            if (fx === 0) {
              return l - i - 1 + parseInt(t.dataset.delay);
            } else if (fx === 1) {
              return parseInt(t.dataset.column) + parseInt(t.dataset.delay);
            } else {
              return (l - i - 1) * 20 + getRandomInt(-30, 30);
            }
          },
          translateX: (t, i) => {
            if (fx === 0) {
              return t.dataset.column <
                piecesObjs[current].getTotalColumns() / 2
                ? anime.random(50, 100) + "px"
                : anime.random(-100, -50) + "px";
            } else if (fx === 1) {
              return pos > current
                ? anime.random(-1500, -1000) + "px"
                : anime.random(1000, 1500) + "px";
            } else {
              return anime.random(-10, 10) + "px";
            }
          },
          translateY: (t, i) => {
            if (fx === 0) {
              return anime.random(-1000, -800) + "px";
            } else if (fx === 1) {
              return t.dataset.row < piecesObjs[current].getTotalRows() / 2
                ? anime.random(50, 100) + "px"
                : anime.random(-100, -50) + "px";
            } else {
              return anime.random(-1000, -800) + "px";
            }
          },
          opacity: {
            value: 0,
            easing: "linear"
          },
          complete: () => {
            galleryItems[current].classList.remove("gallery__item--current");
            current = pos;
            isAnimating = false;
          }
        });

        piecesObjs[pos].pieces.forEach(piece => {
          piece.style.opacity = 0;

          if (fx === 0) {
            piece.style.transform = `translateX(${
              piece.dataset.column > piecesObjs[pos].getTotalColumns() / 2
                ? anime.random(50, 100)
                : anime.random(-100, -50)
            }px) translateY(${anime.random(800, 1000)}px)`;
          } else if (fx === 1) {
            piece.style.transform = `translateX(${
              pos > current
                ? anime.random(1000, 1500)
                : anime.random(-1500, -1000)
            }px) translateY(${
              piece.dataset.row < piecesObjs[pos].getTotalRows() / 2
                ? anime.random(50, 100)
                : anime.random(-100, -50)
            }px)`;
          } else {
            piece.style.transform = `translateX(${anime.random(
              -50,
              50
            )}px) translateY(${anime.random(-200, -100)}px)`;
          }
        });

        galleryItems[pos].style.opacity = 1;
        piecesObjs[pos].animate({
          duration: 800,
          easing: fx === 2 ? "easeOutQuint" : "easeInOutQuint",
          delay: (t, i, l) => {
            if (fx === 0) {
              return i + parseInt(t.dataset.delay);
            } else if (fx === 1) {
              return parseInt(t.dataset.column) + parseInt(t.dataset.delay);
            } else {
              return (l - i - 1) * 40 + 150;
            }
          },
          translateX: 0,
          translateY: 0,
          rotateZ: () => {
            if (fx === 0) {
              return [anime.random(-45, 45), 0];
            }
            return 0;
          },
          opacity: 1,
          complete: () => {
            galleryItems[pos].classList.add("gallery__item--current");
          }
        });
      };

      const fxItems = Array.from(
        document.querySelectorAll(".switch > .switch__item")
      );
      fxItems.forEach((fxitem, pos) => {
        fxitem.addEventListener("click", ev => {
          if (fx === pos) return;
          fxItems[fx].classList.remove("switch__item--current");
          fx = pos;
          fxItems[fx].classList.add("switch__item--current");
        });
      });
    }
    // 模拟自动轮播
    const items = document.querySelectorAll(".gallery-nav__item");
    let item_no = 0;
    const itemsLength = items.length;
    setInterval(() => {
      item_no++;
      if (item_no === itemsLength) {
        item_no = 0;
      }
      items[item_no].click();
    }, 5000);
  }
};
</script>
<style lang="scss" scoped>
