body {
  --body-text: #f1d51d;
  --body-text-alt: #525252;
  --body-bg: #3d3d3d;
  --link-text: #6d6d6d;
  --link-text-hover: #fff;
  --content-title-text: #000;
  --content-subtitle-text: #f1d51d;
  --highlight-text: #ffc712;
  --content-font: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif;
  color: #f1d51d;
  color: var(--body-text);
  background: #3d3d3d;
  background: var(--body-bg);
  font-size: 14px;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: #6d6d6d;
  color: var(--link-text);
  outline: none;
}

a:hover,
a:focus {
  outline: none;
  color: #fff;
  color: var(--link-text-hover);
}

.hidden {
  position: absolute;
  overflow: hidden;
  width: 0;
  height: 0;
  pointer-events: none;
}

.message {
  background: #333;
  background: var(--body-text);
  color: #ccd8e4;
  color: var(--body-bg);
  text-align: center;
  padding: 1em;
  display: none;
}

/* Icons */
.icon {
  display: block;
  width: 1.5em;
  height: 1.5em;
  margin: 0 auto;
  fill: currentColor;
}

/* Page Loader */
.js .loading::before {
  content: "";
  position: fixed;
  z-index: 10000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ccd8e4;
  background: var(--body-bg);
}

.js .loading::after {
  content: "";
  position: fixed;
  z-index: 10000;
  top: 50%;
  left: 50%;
  width: 80px;
  height: 80px;
  margin: -40px 0 0 -40px;
  pointer-events: none;
  background: #333;
  background: var(--body-text);
  transform-origin: 0% 50%;
  animation: loaderAnim 1.5s linear infinite alternate forwards;
}

@keyframes loaderAnim {
  0% {
    transform: scale3d(0, 1, 1);
    transform-origin: 0% 50%;
  }
  50% {
    transform: scale3d(1, 1, 1);
    transform-origin: 0% 50%;
  }
  51% {
    transform: scale3d(1, 1, 1);
    transform-origin: 100% 50%;
  }
  100% {
    transform: scale3d(0, 1, 1);
    transform-origin: 100% 50%;
  }
}

/* Frame */
.frame {
  top: 0;
  left: 0;
  position: fixed;
  z-index: 100;
  display: grid;
  width: 100%;
  height: 100vh;
  z-index: 100;
  pointer-events: none;
  padding: 2em;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto auto 4em;
  grid-template-areas:
    "... header"
    "... header"
    "... demos";
}

.no-js .frame {
  position: relative;
  display: block;
  height: auto;
}

.frame a {
  pointer-events: auto;
}

.frame__title {
  font-family: var(--content-font);
  margin: 0;
  font-size: 2em;
  justify-self: start;
}

/* Header */
.codrops-header {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  grid-area: header;
  align-self: start;
  justify-self: end;
}

.codrops-header__title {
  font-size: 1em;
  opacity: 0;
  margin: 1em 0 0;
  font-weight: normal;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
}

/* Top Navigation Style */
.codrops-links {
  position: relative;
}

.codrops-icon {
  display: block;
  margin: 0.25em 0;
  padding: 0.25em 0;
}

/* Demo links */
.demos {
  grid-area: demos;
  align-self: end;
  justify-self: end;
  margin: 0 0 0 auto;
}

.demo {
  display: inline-block;
  margin: 0 0 0 1em;
  padding: 0 0 0.25em;
  white-space: nowrap;
  border-bottom: 2px solid transparent;
}

.demo--current {
  border-color: currentColor;
}

.pieces {
  display: flex;
  flex-wrap: wrap;
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: 0 0;
}

@media screen and (max-width: 40em) {
  .message {
    display: block;
  }
  .frame {
    display: block;
    height: auto;
    position: relative;
    text-align: center;
    z-index: 10000;
  }
  .codrops-header {
    flex-direction: column;
  }
  .codrops-header__title {
    padding: 1em 0;
    opacity: 1;
    -webkit-writing-mode: horizontal-tb;
    writing-mode: horizontal-tb;
  }
  .codrops-icon {
    display: inline-block;
    margin: 0.25em;
    padding: 0.25em;
  }
  .demos {
    padding: 1em 0 0;
  }
  .demo {
    margin: 0 0.5em 0.5em;
  }
}
.demo-gallery {
  width: 100%;
  height: 100%;
  --body-text: #000;
  --body-text-alt: #354dca;
  --body-bg: #f5f1ed;
  --link-text: #c8bbac;
  --link-text-hover: #000;
  --highlight-text: #354dca;
  --content-font: "Helvetica Neue", Arial, sans-serif;
}

/* Content */
.content {
  position: relative;
  //   display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(7, 1fr);
  height: calc(100vh - 60px);
  min-height: 500px;
  overflow: hidden;
  font-family: var(--content-font);
  padding-top: 1%;
  --pieces-height: 96%;
}

.deco-letter {
  font-size: 10vw;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 10;
  pointer-events: none;
  font-weight: bold;
}

.deco-letter::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 1.5em;
  top: 50%;
  left: 50%;
  margin: -0.75em 0 0 -5px;
  transform: rotate(30deg);
  background: var(--body-text-alt);
  z-index: -1;
}

.deco-letter:first-child {
  grid-area: 1 / 1 / 2 / 3;
}

.deco-letter:nth-child(2) {
  grid-area: 2 / 1 / 4 / 2;
}

.deco-letter:nth-child(3) {
  grid-area: 3 / 2 / 4 / 3;
}

.deco-letter:nth-child(4) {
  grid-area: 2 / 6 / 3 / 7;
}

.deco-letter:nth-child(5) {
  grid-area: 3 / 4 / 5 / 7;
}

.deco-letter:nth-child(6) {
  grid-area: 1 / 5 / 2 / 7;
}

.gallery {
  grid-area: 1 / 3 / 6 / 5;
  height: 100%;
}

.gallery__item {
  opacity: 0;
}

.gallery__item--current {
  opacity: 1;
  z-index: 100;
}

.content .pieces {
  height: var(--pieces-height);
  width: 96%;
  position: absolute;
  //   left: 50%;
  margin-left: 2%;
}

.piece {
  pointer-events: none;
}

.menu {
  z-index: 10;
  grid-area: 2 / 2 / 3 / 4;
}

.gallery-nav {
  grid-area: 6 / 1 / 7 / 7;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2em 0 0;
}

.gallery-nav__item {
  width: 40px;
  height: 20px;
  display: block;
  margin: 10px;
  cursor: pointer;
  position: relative;
}

.gallery-nav__item::before,
.gallery-nav__item::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 6px;
  margin: -3px;
  top: 50%;
  left: 0;
  opacity: 0.5;
  background: var(--link-text);
  pointer-events: none;
}

.gallery-nav__item::after {
  opacity: 0;
  transform: rotate(-60deg);
  background: var(--body-text-alt);
}

.gallery-nav__item--current::before,
.gallery-nav__item--current::after {
  opacity: 1;
}

.gallery-nav__item:hover::before,
.gallery-nav__item--current::before {
  background: var(--link-text-hover);
}

.switch {
  grid-area: 7 / 1 / 8 / 7;
  padding: 2em 0;
  text-align: center;
}

.switch__item {
  border: 0;
  padding: 0.5em 1em;
  margin: 0 0.25em;
  background: none;
  color: var(--link-text);
}

.switch__item:hover,
.switch__item:focus,
.switch__item--current {
  color: var(--body-bg);
  outline: none;
  background: var(--body-text);
}

@media screen and (max-width: 60em) {
  .demo-gallery .pater {
    color: #fff !important;
  }
}

@media screen and (max-width: 40em) {
  .content {
    margin-bottom: 8em;
    height: auto;
    min-height: 0;
    grid-template-columns: 100%;
    grid-template-rows: var(--pieces-height) auto auto;
    --pieces-height: 300px;
  }
  .deco-letter {
    display: none;
  }
}
